import React from 'react';
import './css/main.css'
import './css/infoGroup.css'
import './css/generalStatistics.css'
import Group from './ components/group';
import Infogroup from './ components/infogroup'
import GeneralStatistics from './ components/generalStatistics';


class App extends React.Component{
  constructor(props){
    super(props)
    this.state = {
      clicked: false
    }
    this.onClick = this.onClick.bind(this)
  }
  render(){
    if(this.state.clicked === false){
      return(
        <div>
          <div className="about-page"><p>Турнірна сітка команд</p></div>
          <div className="groupComponentDiv">
            <Group />
            <Group />
            <GeneralStatistics />
          </div>
          <button onClick={this.onClick}>Переглянути статистику матчів</button>
        </div>
      )
    }
    else{
      return(
        <div>
          <div className="about-page"><p>Турнірна сітка команд</p></div>
          <div className='infoGroupComponentDiv'>
            <Infogroup />
            <Infogroup />
            <GeneralStatistics />
          </div>
          <button onClick={this.onClick}>Переглянути турнірну сітку</button>
        </div>
        
      )
    }
      
  }
  onClick(){
    this.setState({clicked: !this.state.clicked})
  }

}

export default App
